.Modal-backdrop {
  background-color: rgba(0,0,0,0.3);
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
  opacity: 0;
  -webkit-animation: backdropIn .25s cubic-bezier(.06,.67,.37,.99) forwards;
          animation: backdropIn .25s cubic-bezier(.06,.67,.37,.99) forwards
}

.Modal {
  background-color: #fff;
  border-radius: 8px;
  max-width: 300px;
  padding: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-animation: modalIn .25s cubic-bezier(.06,.67,.37,.99) forwards;
          animation: modalIn .25s cubic-bezier(.06,.67,.37,.99) forwards
}

.Modal-close_button {
  background-color: rgb(251, 163, 113);
  border-radius: 4px;
  border: 2px solid #0b0b0b;
  -webkit-box-shadow: rgb(255, 132, 75) 0px 0px 0px 3px inset;
          box-shadow: rgb(255, 132, 75) 0px 0px 0px 3px inset;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  padding: 15px 20px;
}

@-webkit-keyframes backdropIn {
  0%   { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes backdropIn {
  0%   { opacity: 0; }
  100% { opacity: 1; }
}

@-webkit-keyframes modalIn {
  0%   { -webkit-transform: translate(-50%, -50%) translateY(10px); transform: translate(-50%, -50%) translateY(10px) }
  100% { -webkit-transform: translate(-50%, -50%); transform: translate(-50%, -50%) }
}

@keyframes modalIn {
  0%   { -webkit-transform: translate(-50%, -50%) translateY(10px); transform: translate(-50%, -50%) translateY(10px) }
  100% { -webkit-transform: translate(-50%, -50%); transform: translate(-50%, -50%) }
}



