.Button {
  background-color: gray;
  border-radius: 4px;
  border: 2px solid #0b0b0b;
  -webkit-box-shadow: rgb(255, 132, 75) 0px 0px 0px 3px inset;
          box-shadow: rgb(255, 132, 75) 0px 0px 0px 3px inset;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  padding: 15px 20px;
}

.Button.is-disabled {
  background-color: #ccc;
  border-color: #999;
  -webkit-box-shadow: #aaa 0px 0px 0px 3px inset;
          box-shadow: #aaa 0px 0px 0px 3px inset;
  color: #eee;
  pointer-events: none;
}


