.TokenItem {
  cursor: pointer;
  position: relative;
  -webkit-animation: tokenItemIn .25s cubic-bezier(.06,.67,.37,.99) forwards;
          animation: tokenItemIn .25s cubic-bezier(.06,.67,.37,.99) forwards
}

.TokenItem:hover > .TokenItem-image_wrapper {
  -webkit-box-shadow: rgb(255, 255, 255) 0px 0px 0px 4px inset;
          box-shadow: rgb(255, 255, 255) 0px 0px 0px 4px inset
}

.TokenItem-image_wrapper {
  background-color: #e7dcd6;
  border-radius: 10px;
  padding: 40px 20px;
  margin-bottom: 15px;
  -webkit-box-shadow: rgb(255, 255, 255) 0px 0px 0px 0px inset;
          box-shadow: rgb(255, 255, 255) 0px 0px 0px 0px inset;
  -webkit-transition: -webkit-box-shadow 0.2s;
  transition: -webkit-box-shadow 0.2s;
  -o-transition: box-shadow 0.2s;
  transition: box-shadow 0.2s;
  transition: box-shadow 0.2s, -webkit-box-shadow 0.2s;
}

.TokenItem-label {
  text-align: left;
}

.token-card {
  background: #fff;
  color: grey;
  padding: 10px;
}

@-webkit-keyframes tokenItemIn {
  0%   { opacity: 0; -webkit-transform: translateY(10px); transform: translateY(10px) }
  100% { opacity: 1; -webkit-transform: translateY(0); transform: translateY(0) }
}

@keyframes tokenItemIn {
  0%   { opacity: 0; -webkit-transform: translateY(10px); transform: translateY(10px) }
  100% { opacity: 1; -webkit-transform: translateY(0); transform: translateY(0) }
}


